@import url("https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:wght@600;700&display=swap");
html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  line-height: 1.3;
  width: 100%;
  min-height: 100vh;
  font-family: "Barlow Semi Condensed", sans-serif;
  background: radial-gradient(at top, #1f3756, #141539);
}

.wrapper {
  width: 100%;
  height: 100%;
  position: relative;
}

header {
  border: 2px solid #606e85;
  border-radius: 0.7rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 85%;
  max-width: 43.75rem;
  min-height: 6.0625rem;
  margin: 0 auto;
  margin-top: 2.125rem;
}
header .logo {
  width: 5rem;
  margin: 1.2rem;
  min-height: 50px;
}
@media (min-width: 769px) {
  header .logo {
    width: 10.625rem;
  }
}
header .score-container {
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 5rem;
  border-radius: 0.6rem;
  padding: 1rem;
  margin: 1rem;
  min-height: 4.5625rem;
}
@media (min-width: 769px) {
  header .score-container {
    width: 9.375rem;
  }
}
header .score-container .score-text {
  color: #2a46c0;
  font-size: 0.625rem;
  letter-spacing: 1px;
  text-transform: uppercase;
}
@media (min-width: 769px) {
  header .score-container .score-text {
    font-size: 0.875rem;
  }
}
header .score-container .score-number {
  font-size: 1.875rem;
  color: #3b4363;
  font-weight: bold;
}
@media (min-width: 769px) {
  header .score-container .score-number {
    font-size: 2.9375rem;
  }
}

.game-container {
  width: 100%;
  padding: 1rem;
  max-width: 31.25rem;
  min-height: 17.5rem;
  margin: 6.0625rem auto;
  display: grid;
  grid-template-areas: "paper scissor" "rock rock";
  gap: 1rem;
  position: relative;
}
@media (min-width: 769px) {
  .game-container {
    gap: 5rem;
    margin: 2rem auto;
  }
}
.game-container::before {
  content: " ";
  position: absolute;
  width: 50%;
  height: 70%;
  top: 55%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: url(./images/bg-triangle.svg) no-repeat;
  background-size: contain;
  z-index: -1;
}
@media (min-width: 769px) {
  .game-container::before {
    width: 100%;
    height: 70%;
    top: 55%;
    left: 75%;
  }
}

.choice-btn {
  width: 6.25rem;
  margin: 0 auto;
  border: none;
  outline: none;
  background: none;
  cursor: pointer;
}

.choice-btn[data-armor=paper] {
  grid-area: paper;
}

.choice-btn[data-armor=scissors] {
  grid-area: scissor;
}

.choice-btn[data-armor=rock] {
  grid-area: rock;
}

.choice-img {
  position: relative;
  width: 6.25rem;
  height: 6.25rem;
  background: #fff;
  border-radius: 50%;
  display: grid;
  place-items: center;
  box-shadow: inset 0 0.36rem rgba(0, 0, 0, 0.3);
}
@media (min-width: 769px) {
  .choice-img {
    width: 8.75rem;
    height: 8.75rem;
  }
  .choice-img img {
    width: 4.375rem;
    height: 4.375rem;
  }
}
.choice-img::before, .choice-img::after {
  content: " ";
  position: absolute;
  left: -15%;
  top: -15%;
  width: 130%;
  height: 130%;
  border-radius: 50%;
  z-index: -1;
  box-shadow: inset 0 -0.36rem rgba(0, 0, 0, 0.4);
}
.choice-img[data-paper]::before {
  background: linear-gradient(to bottom, #4865f4, #5671f5);
}
.choice-img[data-scissors]::before {
  background: linear-gradient(to bottom, #ec9e0e, #eca922);
}
.choice-img[data-rock]::before {
  background: linear-gradient(to bottom, #dc2e4e, #dd405d);
}
.choice-img::after {
  opacity: 0;
  transition: opacity 400ms ease;
}

.choice-btn:focus .choice-img::after {
  opacity: 1;
  box-shadow: 0 0 0 2rem #223351;
  z-index: -2;
}

.rules-container {
  width: 100%;
  padding: 2rem;
  display: flex;
  justify-content: center;
}
.rules-container .rules-btn {
  width: 8.125rem;
  height: 2.5rem;
  color: #fff;
  text-transform: uppercase;
  font-size: 0.875rem;
  font-family: inherit;
  letter-spacing: 1px;
  background: none;
  outline: none;
  border: 1px solid #fff;
  border-radius: 0.4rem;
}
.rules-container .rules-btn:focus {
  border: 5px solid #fff;
}
.rules-container .rules-btn:hover {
  cursor: pointer;
}
@media (min-width: 769px) {
  .rules-container .rules-btn {
    position: absolute;
    bottom: 0;
    right: 0;
    margin-right: 2rem;
    margin-bottom: 2rem;
  }
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  background-color: rgba(255, 255, 255, 0.7);
  display: none;
}
.modal-overlay .modal-wrap {
  background: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 3.75rem 0;
}
@media (min-width: 769px) {
  .modal-overlay .modal-wrap {
    position: relative;
    padding: 2rem;
    align-items: flex-start;
  }
  .modal-overlay .modal-wrap img {
    margin: 0 auto;
  }
}
.modal-overlay .modal-wrap .rules-text {
  font-size: 1.5rem;
  text-transform: uppercase;
  margin-top: 1rem;
  color: #3b4363;
}
@media (min-width: 769px) {
  .modal-overlay .modal-wrap .rules-text {
    margin-top: 0;
  }
}
.modal-overlay .modal-wrap .close-btn {
  background: none;
  border: none;
  outline: none;
}
.modal-overlay .modal-wrap .close-btn:hover {
  cursor: pointer;
}
@media (min-width: 769px) {
  .modal-overlay .modal-wrap .close-btn {
    position: absolute;
    top: 2.5rem;
    right: 2rem;
  }
}
@media (min-width: 769px) {
  .modal-overlay .modal-wrap {
    width: 25.3125rem;
    height: 26.25rem;
    border-radius: 0.5rem;
  }
}

.show-modal {
  display: block;
}
@media (min-width: 769px) {
  .show-modal {
    background-color: rgba(0, 0, 0, 0.3);
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
}

.result {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-areas: "user-armor computer-armor" "user           computer   ";
  width: 100%;
  max-width: 1000px;
  margin: 0 auto;
  gap: 2.5rem;
  padding: 4rem 2rem;
  color: #fff;
  text-align: center;
  display: none;
}
.result > * {
  padding: 1rem;
}
.result h1 {
  font-size: 2.5625rem;
  text-transform: uppercase;
  font-weight: 700;
}
@media (min-width: 769px) {
  .result h1 {
    font-size: 3.125rem;
  }
}
.result h2 {
  font-size: 0.8125rem;
}
@media (min-width: 769px) {
  .result h2 {
    font-size: 1.125rem;
  }
}

.user-picked {
  grid-area: user-armor;
  min-height: 160px;
  height: auto;
}

h2[data-user-pick] {
  grid-area: user;
}

.comp-picked {
  grid-area: computer-armor;
  min-height: 160px;
  height: auto;
}

h2[data-comp-pick] {
  grid-area: computer;
}

[data-play-again-btn] {
  text-transform: uppercase;
  background-color: #fff;
  width: 13.875rem;
  font-family: inherit;
  font-size: 1rem;
  padding: 1rem 0;
  border-radius: 0.5rem;
  border: none;
  outline: none;
  margin-top: 2rem;
}
[data-play-again-btn]:hover {
  cursor: pointer;
  color: #dd405d;
}
@media (min-width: 769px) {
  [data-play-again-btn] {
    font-size: 1.25rem;
  }
}

.result-item {
  width: 10rem;
  height: 10rem;
  background: #16213d;
  border-radius: 50%;
  z-index: -1;
}

.show-result {
  display: grid;
  grid-template-areas: "user-armor computer-armor" "user           computer " "result       result";
}
@media (min-width: 769px) {
  .show-result {
    grid-template-columns: repeat(3, 1fr);
    grid-template-areas: "user  result   computer" "user-armor        result   computer-armor";
    place-items: center;
  }
}

.hidden {
  display: none;
}

.show-winner {
  grid-area: result;
}

.winner .choice-img::after {
  opacity: 1;
  box-shadow: 0 0 0 20px #293251, 0 0 0 70px #232c4b, 0 0 0 100px #1e2949;
  z-index: -99;
}
@media (min-width: 769px) {
  .winner .choice-img::after {
    box-shadow: 0 0 0 50px #293251, 0 0 0 100px #232c4b, 0 0 0 170px #1e2949;
  }
}